export default {
	data() {
		return {
			active:1,
			activeClass:"info-header-btn-active",
			supplyList:[],
			ListLen:0,
			supplyList2:[],
			ListLen1:0,
			shrink:"",
		}
	},
	onLoad:function(){
		var _self = this
		// wx.showShareMenu() 
		// if(!uni.getStorageSync("openId")){
		// 	uni.navigateTo({
		// 		url:"../load/load"
		// 	})
		// }
		_self.getInfo()
		_self.getInfo1()
	},
	onPullDownRefresh:function() {
		var _self = this
		// if(!uni.getStorageSync("openId")){
		// 	uni.navigateTo({
		// 		url:"../load/load"
		// 	})
		// }
		_self.getInfo()
		_self.getInfo1()
	},
	methods: {
		changeActive:function(i){
			this.active=i
		},
		getInfo:function(){
			var _self = this
			uni.request({
				url:_self.$api+"dockingManager/declareQuery",
				data:{id:"0",mark:0,optionId:uni.getStorageSync("openId")}, 
				method:"GET",
				success:function(res){
					_self.supplyList = res.data
					_self.shrink = res.data
					_self.ListLen = res.data.length
					console.log(res.data) 
				} 
			})
		}, 
		getInfo1:function(){
			var _self = this
			uni.request({
				url:_self.$api+"dockingManager/declareQuery",
				data:{id:"0",mark:1,optionId:uni.getStorageSync("openId")},
				method:"GET",
				success:function(res){
					_self.supplyList2 = res.data
					_self.ListLen1 = res.data.length
					console.log(res.data)
					 uni.stopPullDownRefresh();
				}
			})
		},
		lookDetail:function(id,mark){
			uni.navigateTo({
				url:"../infoPage/infoDetail/infoDetail?id="+id+"&mark="+mark
			})
		}
	},
	filters:{
		getPic:function(res){
			return res.split(',')[0]
		},
		getTime:function(res){
			return res.slice(0,11)
		}
	}
}
